



















import {
  Component,
  Vue,
} from 'vue-property-decorator'
import {
  getRecyclingSend
} from "@/api/modules/global";

@Component({})
export default class HomeRecycling extends Vue {
  isshow: boolean = false
  image: string = ""

  // 组件周期函数--监听组件挂载完毕
  mounted() {
    // this.getProperties(this.decoretionProperties)
    this.image = this.$STORE.setStore.recyclingsetting.homePromptBgImg || "";
    if (this.$STORE.userStore.userInfo.token) {
      // this.getCouponList();
      this.isshow = false
    } else {
      this.appLaunchInit();
    }
  }
  getProperties(formdata) {
    this.isshow = formdata.isshow
    this.image = formdata.image
  }

  appLaunchInit() {
    const openrecyling = this.$STORE.setStore.recyclingsetting.recyclingState;
    this.$Pubsub.on("app-launch", () => {
      getRecyclingSend().then(res => {
        this.isshow = res && openrecyling
        this.image = this.$STORE.setStore.recyclingsetting.homePromptBgImg
      });
    });
  }

  cancel() {
    this.isshow = false
  }

  topage() {
    this.isshow = false
    uni.navigateTo({
      url: "/subcontract/pages/mysend/mysend",
    });
  }
}
